body {
    background-color: black;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
  }
}

.App-header {
  background-color: #000000;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(3px + 2vmin);
  color: white;
}

.App-link {
  font-size: 15px;
  color: #61dafb;
}

.message {
    font-family: 'Bungee Inline', cursive;
    text-align: center;
    width: 30ch;
    white-space: nowrap;
    overflow: hidden;
    animation: typing 2s steps(26, end);
}

@keyframes typing {
    from {
        width: 0;
    }
}

.story {
    font-family: 'Bungee Inline', cursive;
    animation: flicker 1.5s infinite alternate;
}

ul {
    text-align: left;
}

@keyframes flicker {
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
      text-shadow:
        0 0 4px #fff,
        0 0 11px #fff,
        0 0 19px #fff,
        0 0 40px #0fa,
        0 0 80px #0fa,
        0 0 90px #0fa,
        0 0 100px #0fa,
        0 0 150px #0fa;
    }
    20%, 24%, 55% {       
      text-shadow: none;
    }
  }